body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  color: #444; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Bitter', serif; }

input {
  font-size: inherit;
  text-align: center;
  max-width: 4em; }

.App {
  text-align: center; }

.App-header {
  background: #4CB8C4;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #3CD3AD, #4CB8C4);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#3CD3AD), to(#4CB8C4));
  background: -webkit-linear-gradient(left, #3CD3AD, #4CB8C4);
  background: -o-linear-gradient(left, #3CD3AD, #4CB8C4);
  background: linear-gradient(to right, #3CD3AD, #4CB8C4);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

.container {
  max-width: 960px;
  margin: auto; }

.grid {
  display: grid;
  margin-bottom: 1em; }

.grid--three {
  grid-template-columns: 1fr 1fr 1fr; }
  @media screen and (max-width: 700px) {
    .grid--three {
      grid-template-columns: none; } }



